  background-size: 100% 100%;
  background-position: center;
  transition: 0.4s all ease;
  height: 150px;
  width: 150px;
  opacity: 0;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
  pointer-events: none;
}

.map-icons-container .btn .logo-animation {
  -webkit-animation-name: logo-clicked-animation;
          animation-name: logo-clicked-animation;
}

@-webkit-keyframes logo-clicked-animation {
  0% {
    height: 150px;
    width: 150px;
  }
  50% {
    height: 170px;
    width: 170px;
  }
  100% {
    height: 150px;
    width: 150px;
  }
}

@keyframes logo-clicked-animation {
  0% {
    height: 150px;
    width: 150px;
  }
  50% {
    height: 170px;
    width: 170px;
  }
  100% {
    height: 150px;
    width: 150px;
  }
}

